@mixin display($display,$justify-content:none,
$align-items:none,$wrap:nowrap,$f-der:none) {
   display: $display;
   justify-content: $justify-content;
   align-items: $align-items;
   flex-wrap: $wrap;
   flex-direction: $f-der;
}

@mixin position($position,$top:none,$right:none,$left:none) {
   position: $position;
   top: $top;
   right: $right;
   left: $left;
}

@mixin padding($pad...) {
   padding: $pad;
}

@mixin text($size,$weight:none,$line:none) {
   font-size: $size;
   font-weight: $weight;
   line-height: $line;
}

@mixin container() {
   height: 100%;
   width: 80%;
   display: flex;
   align-items: center;
   flex-wrap: wrap;
   margin: 0 auto;
   @media screen and (max-width: 900px) {
      width: 80%
   }
   @media screen and (max-width: 450px) {
      width: 92%
   }
}

@mixin navStyle($transform:scaleX(0),$width:100%) {
   content: "";
   height: 3px;
   background: #f9004d;
   transition: .3s;
   bottom: 0;
   width: $width;
   transform: $transform;
   transform-origin: left;
}

